export default {
	data() {
		return {
			tabIndex: 0,
			cateList: [{
				_id: '',
				name: '全部'
			}],
			list: [],

			loadMoreText: "加载中...",
			showLoadMore: false,
			skip: -1,
			max: false //是否已经达到最大值 没有更多了
		}
	},
	onLoad() {
		uni.request({
			url: this.apiBasic + '/cate',
			method: 'GET',
			success: res => {
				console.log(res);
				this.cateList = [...this.cateList, ...res.data];
			},
			fail: () => {},
			complete: () => {}
		});

		this.getArticleList()
	},
	onUnload() {
		this.max = false,
			this.list = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.max) {
			return;
		}

		this.showLoadMore = true;
		setTimeout(() => {
			this.getArticleList()
		}, 300);
	},
	methods: {
		tapBar(_id, index) {
			//通过分类编号 获取文章列表
			if (this.tabIndex == index) {
				return;
			}

			this.tabIndex = index;
			this.skip = -1;
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
			this.getArticleList(_id)

		},
		getArticleList(cateId) {
			this.skip++;
			
			
			let params = {
				skip: this.skip
			}
			if(cateId){
				params.cate_id = cateId;
			}

			uni.request({
				url: this.apiBasic + '/articles',
				method: 'GET',
				data: params,
				header: {
					'Authorization': 'Bearer ' + uni.getStorageSync("SToken")
				},
				success: res => {
					
					if(this.skip == 0){
						this.list = [];
					}

					let data = res.data;
					if (data.length == 0) {
						this.showLoadMore = true;
						this.loadMoreText = "没有更多数据了!"
						this.max = true;
						return;
					}

					data.createdAt = new Date(data.createdAt).toLocaleString();

					data.forEach((item, index) => {
						let description = JSON.parse(item.description)
						description = description.filter(item => item.type == "image");
						if (description.length >= 3) {
							item.article_type = 4;
							item.image_list = description.slice(0, 3);
						} else {
							
							item.article_type = Math.ceil(Math.random() * 3);
							console.log(item.article_type);
							item.image_url = description[0].content;
						}

						data[index] = item;
					})

					this.list = this.list.concat(data);
				},
				fail: () => {},
				complete: () => {}
			});
		}

	}
}
